<template>
  <header class="bg-dark text-white p-3 mb-5 text-uppercase">
    <div class="d-flex align-items-center justify-content-between flex-column flex-md-row">
      <h1 class="mb-0">{{ $t('header.title') }}</h1>
      <nav class="nav">
        <a class="nav-link text-white" :class="[locale === 'en' ? 'active' : '']" @click="switchLanguage('en')">{{ $t('header.english')}}</a>
        <a class="nav-link text-white" :class="[locale === 'fr' ? 'active' : '']"  @click="switchLanguage('fr')">{{ $t('header.french')}}</a>
      </nav>
    </div>
  </header>
</template>

<script>
import { useI18n } from 'vue-i18n';
export default {
  name: "Header",
  setup() {
    const { t, locale } = useI18n();
    
    const switchLanguage = (lang) => {
      locale.value = lang;
    };
    
    return {
      switchLanguage,
      t,
      locale,
    };
  },
};
</script>

<style scoped>
.nav-link {
  margin-right: 15px;
  font-weight: bold;
}
a {
  cursor:pointer
}
.active {
  pointer-events: none;
  opacity: 0.5;
}
@media (max-width: 425px) {

h1 {
  display: none;
}

nav {
  padding: .25rem;
}

}
</style>
